// check selected sku
export const selectedSkuCheck = ({ moduleEntry }) => {
    let count = 0
    if (moduleEntry) {
        moduleEntry.jobPost.list.map(data => {
            if (data.checked) count = count + 1
        })
    }

    return count
}

//add new SKU
export const addSku = async ({ dispatch, moduleEntry, moduleSummary, moduleName }) => {
    let jobSheet = { ...moduleSummary }
    moduleEntry.map((data) => {
        jobSheet.jobPost.list.push(data)
    })
    const filtered = jobSheet.jobPost.list?.sort(function (a, b) { return filterBySkuName(a, b) })
    jobSheet.jobPost.list = filtered
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: jobSheet.jobPost.list.length > 0 ? jobSheet : null });
}

// get selected sku
export const getEditedData = ({ data }) => {
    let edited = false;
    const a = data?.poList?.map(item => {
        if (item.edited) edited = true
    })
    return edited
}

export const submitValidation = ({ setActiveSKU, moduleEntry, moduleName, setConfirmation, dispatch, setIsRequired }) => {
    let isClear = []
    let js = { ...moduleEntry }

    const jsR = moduleEntry.jobPost.list.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                const { qty, grade, ripeness, note } = item

                if (po.category === 'Fruits') {
                    if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                        item.qtyIsEmpty = false
                        item.gradeIsEmpty = false
                        item.ripenessIsEmpty = false
                        item.noteIsEmpty = false
                    }
                    else {
                        if (item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste') {
                            isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                            !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                            grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        }
                        else {
                            isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                            !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                            !ripeness?.value ? item.ripenessIsEmpty = true : item.ripenessIsEmpty = false
                            grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        }
                    }
                }
                else {

                    if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                        item.qtyIsEmpty = false
                        item.gradeIsEmpty = false
                        item.ripenessIsEmpty = false
                        item.noteIsEmpty = false
                    }
                    else {
                        isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                    }
                }

                if (item?.qtyIsEmpty || item?.gradeIsEmpty || item?.ripenessIsEmpty || item?.noteIsEmpty || item?.qtyToCompareIsEmpty) isClear.push(false)
                else isClear.push(true)
                return item
            })
            return po
        })
        return data
    })


    //validate there is value
    let valid = false
    jsR.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                let i = false
                if (item?.qty >= 0) valid = true
            })
        })
    })

    js.jobPost.list = jsR
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
    if (!isClear.includes(false) && valid) {
        if (typeof (setConfirmation) === 'function') setConfirmation({ show: true, name: 'submit' })
    }
    else {
        if (typeof (setConfirmation) === 'function') setConfirmation({ show: false, name: null })
        if (typeof (setIsRequired) === 'function') setIsRequired(true)
    }

    checkIsSkuRequired({ setActiveSKU, moduleEntry, moduleName })
}

export const totalWeightCount = ({ moduleEntry, activeSku }) => {
    let total = 0
    if (moduleEntry) {
        moduleEntry.jobPost.list[activeSku].poList.map(po => {
            po.itemList.map(item => {
                const qqq = Number.isInteger(item.qty) ? parseInt(item.qty) : Number.parseFloat(item.qty ? item.qty : 0)
                total += qqq
            })
        })
    }
    total = Number.isInteger(total) ? total : Number.parseFloat(total).toFixed(2)
    return total
}

