export const addSlot = ({ dispatch, moduleEntry, slot, moduleName }) => {
    let newModuleEntry = { ...moduleEntry }

    if (slot?.length) {
        newModuleEntry.jobPost.slot = slot.map(s => {
            if (Number.isInteger(s?.value)) return Number.parseInt(s?.value)
            else return 0
        })
        dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: newModuleEntry })
    }
}

export const filterBySkuName = (a, b, type) => {
    var nameA = a.skuName.toUpperCase(); // ignore upper and lowercase
    var nameB = b.skuName.toUpperCase(); // ignore upper and lowercase
    if (nameA < nameB) {
        if (type === 'desc') return 1
        return -1;
    }
    if (nameA > nameB) {
        if (type === 'desc') return -1
        return 1;
    }

    // names must be equal
    return 0;
}

export const filterByRemainingTarget = (a, b, type, moduleName) => {
    var nameA = moduleName === 'stockMovement' ? a.targetMovementProgress[0]?.totalMove : a.targetRepackProgress[0]?.totalPack // ignore upper and lowercase
    var nameB = moduleName === 'stockMovement' ? a.targetMovementProgress[0]?.totalMove : b.targetRepackProgress[0]?.totalPack // ignore upper and lowercase
    if (nameA < nameB) {
        if (type === 'tdesc') return -1
        return 1;
    }
    if (nameA > nameB) {
        if (type === 'tdesc') return 1
        return -1;
    }

    // names must be equal
    return 0;
}

export const filterCheckedSku = ({
    localEntryData,
    moduleName,
    summary,
    dispatch,
    setLocalEntryData,
    warehouseUuid,
    areaId,
    areaIdDesposition
}) => {
    const filteredSku = summary?.filter(data => data.checked)
    let module = {
        jobPost: {
            id: 0,
            warehouseSource: warehouseUuid,
            warehouseDestination: warehouseUuid,
            areaSource: areaId?.value,
            areaName: areaId?.label,
            areaDestination: areaIdDesposition?.value,
            areaDestinationName: areaIdDesposition?.label,
            list: filteredSku,
        }
    }

    const isExist = []
    let a = localEntryData?.jobPost?.list.map(data => isExist.push(data.skuName))

    if (localEntryData?.jobPost?.list?.length) {
        module = { ...localEntryData }
        const f = filteredSku?.filter(data => !isExist.includes(data.skuName))
        module.jobPost.list = module.jobPost.list.concat(f)
    }
    setLocalEntryData(module)
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: module })
}

export const filterCheckedAddSku = ({
    localEntryData,
    moduleEntry,
    moduleName,
    summary,
    dispatch,
    setLocalEntryData,
    warehouseUuid,
    areaId,
    areaIdDesposition
}) => {
    let filteredSku = summary?.filter(data => data.checked)

    const module = {
        jobPost: {
            id: 0,
            warehouseSource: warehouseUuid,
            warehouseDestination: warehouseUuid,
            areaSource: areaId?.value,
            areaName: areaId?.label,
            areaDestination: areaIdDesposition?.value,
            areaDestinationName: areaIdDesposition?.label,
            list: filteredSku,
        }
    }
    const newModuleEntry = { ...moduleEntry }

    if (filteredSku?.length) {
        filteredSku = filteredSku?.map(data => {
            data.isNewItem = true
            return data
        })
        newModuleEntry.jobPost.list = newModuleEntry.jobPost.list.concat(filteredSku)
    }

    if (localEntryData?.jobPost?.list?.length) {
        filteredSku = localEntryData?.jobPost?.list?.map(data => {
            data.isNewItem = true
            return data
        })
        newModuleEntry.jobPost.list = newModuleEntry.jobPost.list.concat(filteredSku)
    }
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: newModuleEntry })
}

export const setSingleViewSku = async ({
    summary,
    setLocalEntryData,
    warehouseUuid,
    areaId,
    areaIdDesposition,
    skuUuId,
    dispatch,
    moduleName
}) => {
    let filteredSku = summary?.filter(data => data.skuUuid === skuUuId)

    const grade = await getGrade({ skuId: filteredSku[0].skuUuid })
    const data = await getPoBySkuId({ skuId: filteredSku[0].skuUuid, warehouseUuid, dispatch, areaId: areaId.value, moduleName })
    if (data) {
        filteredSku[0].gradeList = grade
        const addPoItem = data?.poItems.map(poList => {
            poList.itemList = []
            const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
            const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
            poList.targetMovements = data?.targetMovements
            poList.expiryDate = expiryMs
            poList.inboundDate = inBoundyMs
            poList.skuUuid = filteredSku[0].skuUuid
            poList.uom = filteredSku[0].unitOfMeassures
            poList.category = filteredSku[0].category
            poList.initialWeight = Number.isInteger(poList.initialWeight) ? parseInt(poList.initialWeight) : parseFloat(poList.initialWeight)
            return poList
        })
        filteredSku[0].poList = addPoItem
    }
    const module = {
        jobPost: {
            id: 0,
            warehouseSource: warehouseUuid,
            warehouseDestination: warehouseUuid,
            areaSource: areaId?.value,
            areaName: areaId?.label,
            areaDestination: areaIdDesposition?.value,
            areaDestinationName: areaIdDesposition?.label,
            list: filteredSku,
        }
    }
    setLocalEntryData(module)
}

export const getPoList = async ({ dispatch, moduleName, moduleEntry, activeSku }) => {
    let newEntry = { ...moduleEntry }
    const { areaSource } = newEntry.jobPost
    const { skuUuid, warehouseId, poList } = newEntry.jobPost.list[activeSku]
    if (poList?.length) return

    const grade = await getGrade({ skuId: newEntry.jobPost.list[activeSku].skuUuid })
    const data = await getPoBySkuId({ skuId: skuUuid, warehouseUuid: warehouseId, dispatch, areaId: areaSource, moduleName })
    if (data) {
        newEntry.jobPost.list[activeSku].gradeList = grade
        const addPoItem = data?.poItems.map(poList => {
            poList.itemList = []
            const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
            const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
            poList.targetMovements = data?.targetMovements
            poList.expiryDate = expiryMs
            poList.inboundDate = inBoundyMs
            poList.skuUuid = newEntry.jobPost.list[activeSku].skuUuid
            poList.uom = newEntry.jobPost.list[activeSku].unitOfMeassures
            poList.category = newEntry.jobPost.list[activeSku].category
            poList.initialWeight = Number.isInteger(poList.initialWeight) ? parseInt(poList.initialWeight) : parseFloat(poList.initialWeight)
            return poList
        })
        newEntry.jobPost.list[activeSku].poList = addPoItem
    }
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: newEntry })
}

export const getPoRepack = async ({ dispatch, moduleName, moduleEntry, activeSku }) => {
    let newEntry = { ...moduleEntry }
    const { areaSource } = newEntry.jobPost
    const { skuUuid, warehouseId, poList, unitOfMeassures, packSize, category } = newEntry.jobPost.list[activeSku]
    if (poList?.length) return

    const grade = await getGrade({ skuId: skuUuid })
    const data = await getPORepacking({ skuId: skuUuid, warehouseId, uom: unitOfMeassures })
    const a = await getPORepacking({ skuId: skuUuid, warehouseId, uom: unitOfMeassures })
    newEntry.jobPost.list[activeSku].gradeList = grade
    const addPoItem = [{
        itemList: [],
        expiryMs: null,
        inBoundyMs: null,
        expiryDate: null,
        inboundDate: null,
        packSize: packSize,
        category: category,
        initialWeight: Number.isInteger(data?.data.totalWeight) ? parseInt(data?.data.totalWeight) : parseFloat(data?.data.totalWeight),
        targetRepacks: data?.data.targetRepacks,
        skuUuid: skuUuid,
        uom: data?.data?.uom,
    }]
    newEntry.jobPost.list[activeSku].poList = addPoItem
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: newEntry })
}

