export const resetJobSheet = ({ localEntryData, dispatch }) => {
    let newLocalEntryData = { ...localEntryData }
    newLocalEntryData.jobPost.list = newLocalEntryData.jobPost.list.map(sku => {
        sku.poList.map(po => {
            po.itemList.map(item => {
                item.qtyIsEmpty = false
                item.qtyIsEmpty = false
                item.gradeIsEmpty = false
                item.ripenessIsEmpty = false
                item.noteIsEmpty = false
                item.qtyToCompareIsEmpty = false

                item.qty = po.initialWeight
                item.ripeness = {}
                item.expiryDate = po?.expiryDate
                item.expiryDateText = dateDMY({ date: po?.expiryDate })
                return item
            })
            return po
        })
        return sku
    })
    dispatch({ type: CREATE_JOB_SHEET, stockTakeEntryData: localEntryData })
}

export const checkRequiredData = ({ moduleEntry }) => {
    let requiredSku = []
    let requiredName = []
    let requiredPo = []
    let requiredItem = []

    moduleEntry.jobPost.list.map((data, idx) => {
        data.poList.map((data2, idx2) => {
            data2.itemList.map((data3, idx3) => {
                if (data3.qtyIsEmpty || data3.gradeIsEmpty || data3.ripenessIsEmpty || data3.reasonIsEmpty || data3.noteIsEmpty || data3.packIsEmpty || data3.packThresholdIsEmpty) {
                    requiredSku.push(idx)
                    requiredPo.push(idx2)
                    requiredItem.push(idx3)

                    if (data3.gradeIsEmpty) {
                        requiredName.push('Grade')
                    }

                    if (data3.packIsEmpty || data3.packThresholdIsEmpty) {
                        requiredName.push('pack')
                    }

                    if (data3.qtyIsEmpty) {
                        if (data2.uom === 'gram' || data2.uom === 'kg') requiredName.push('wgt')
                        else requiredName.push('qty')
                    }

                    if (data3.reasonIsEmpty) {
                        requiredName.push('Reason')
                    }

                    if (data3.ripenessIsEmpty) {
                        requiredName.push('Ripeness')
                    }

                    if (data3.noteIsEmpty) {
                        requiredName.push('note')
                    }


                }
            })
        })
    })

    return { requiredSku, requiredName, requiredPo, requiredItem }
}

export const checkIsSkuRequired = async ({ setActiveSKU, moduleEntry }) => {
    const { requiredSku, requiredName, requiredPo, requiredItem } = await checkRequiredData({ moduleEntry })

    if (requiredSku?.length) {
        if (typeof (setActiveSKU) === 'function') await setActiveSKU(requiredSku[0])
        const input = await document.getElementById(`${requiredName[0]}_${requiredPo[0]}_${requiredItem[0]}`)
        if (input) input.focus()
    }
}

export const updateInitialWeight = ({ data, moduleEntry, moduleName }) => {
    const updatedJobSheet = { ...data?.data }
    const currentJobSheet = Object.assign({}, JSON.parse(JSON.stringify(moduleEntry)))
    updatedJobSheet.list.map((updated) => {
        updated.poList.map(updatedPo => {
            currentJobSheet.jobPost.list = currentJobSheet.jobPost.list.map((current) => {
                current.poList.map(currentPo => {
                    let totalQty = 0
                    const updatedPoExpiryDate = updatedPo?.expiryDate ? updatedPo.expiryDate : null
                    currentPo.itemList.map(item => totalQty += Number(item.qty))
                    if (updated.skuUuid === current.skuUuid && updatedPo.wmsPoSkuCode === currentPo.wmsPoSkuCode && currentPo.codeGrade === updatedPo.codeGrade && currentPo?.expiryDate === updatedPoExpiryDate && updatedPo.initialWeight !== currentPo.initialWeight && moduleName !== 'repack') {
                        const moduleQty = updatedPo.initialWeight ? updatedPo.initialWeight : currentPo.initialWeight
                        currentPo.initialWeight = moduleQty
                        currentPo.initialWeightFormatted = moduleQty?.toString()
                        currentPo.initialWeightScreenUpdate = (moduleQty - totalQty).toString()
                        return currentPo
                    }
                    else if (updated.skuUuid === current.skuUuid && updatedPo.wmsPoSkuCode === currentPo.wmsPoSkuCode && currentPo.codeGrade === updatedPo.codeGrade && currentPo?.expiryDate === updatedPoExpiryDate && updatedPo.initialWeight === currentPo.initialWeight && moduleName !== 'repack') {
                        return currentPo
                    }
                    else if (updated.skuUuid === current.skuUuid && updatedPo.wmsPoSkuCode === currentPo.wmsPoSkuCode && currentPo.codeGrade === updatedPo.codeGrade && currentPo?.expiryDate === updatedPoExpiryDate && moduleName === 'repack') {
                        const repackQty = updated.totalQty > 0 ? updated.totalQty : current.totalQty
                        currentPo.initialWeight = repackQty
                        currentPo.totalQty = repackQty
                        currentPo.initialWeightFormatted = repackQty?.toString()
                        currentPo.initialWeightScreenUpdate = (repackQty - totalQty).toString()
                        return currentPo
                    }
                    return currentPo
                })
                return current
            })

        })
    })
    console.log(currentJobSheet);
    currentJobSheet.jobPost.list = currentJobSheet?.jobPost?.list?.map(sku => {
        let totalQty = 0
        sku.poList.map(po => totalQty += Number(po.initialWeight))
        sku.totalQty = totalQty > 0 ? totalQty : sku.totalQty
        return sku
    })
    return currentJobSheet
}